import apiClient from '@src/background/_internal/_api'
import { with403Handler } from '@src/shared/utils/with403Handler'

import { type Since, type Tag } from '@toggl/track-types'

import { BaseListService } from '../lib/BaseListService'
import { useBrowserStorageLocal } from '../lib/use-browser-storage'

class ListService<K extends string> extends BaseListService<
  Record<number, Tag>,
  K
> {
  invoke = with403Handler(async (since: Since) => {
    const data = await apiClient.me.tags.list(since).then(({ data }) => {
      return Object.fromEntries(
        data.map((item) => [item.id, item])
      ) as unknown as Record<number, Tag>
    })

    // Update the internal since key
    await this.arrange(since, data)

    return this.retrieve()
  })
}

const TagsListService = new ListService('FETCH_TAGS_V4')

type StorageData = Awaited<ReturnType<typeof TagsListService['invoke']>>

const useTagsListStorage = (initial: StorageData | null = null) => {
  return useBrowserStorageLocal<StorageData>(
    TagsListService.storageKey,
    initial
  )
}

export { TagsListService, useTagsListStorage }
