import { logger } from '@src/lib/errorReport'
import Browser from 'webextension-polyfill'

import { type Since } from '@toggl/track-types'

const __DEV__ = process.env.NODE_ENV === 'development'

const debugOnDev = (...messages: unknown[]) => {
  if (__DEV__) {
    logger.debug(...messages)
  }
}

class BaseListService<
  Data extends Record<string | number, unknown>,
  K extends string
> {
  public readonly eventKey: `${K}_E`
  public readonly sinceKey: `${K}_S`
  public readonly storageKey: `${K}_D`

  constructor(public readonly identifier: K) {
    this.eventKey = `${identifier}_E`
    this.sinceKey = `${identifier}_S`
    this.storageKey = `${identifier}_D`
  }

  async retrieve() {
    const data = await Browser.storage.local.get(this.storageKey)

    if (this.storageKey in data) {
      return data[this.storageKey] as Data
    }

    return null
  }

  async arrange(since: Since, data: Data) {
    const invokedAt = Math.floor(Date.now() / 1000)

    debugOnDev(`${this.identifier}:: arrange`, { since, invokedAt, data })

    await Browser.storage.local.set({
      [this.sinceKey]: invokedAt,
    })

    if (since !== null) {
      const current = await this.retrieve()

      if (current !== null) {
        await this.store(this.cleanup(Object.assign(current, data)))
      }
    } else {
      await this.store(this.cleanup(data))
    }
  }

  cleanup(data: Data) {
    const cleaned = Object.fromEntries(
      Object.entries(data).filter(([, item]) => {
        if (typeof item !== 'object') {
          return true
        }

        if (!item) {
          return false
        }

        const isOutdated =
          ('deleted_at' in item && item.deleted_at !== null) ||
          ('server_deleted_at' in item && item.server_deleted_at !== null)

        if (isOutdated) {
          debugOnDev(`${this.identifier}:: cleanup`, { item })
        }

        return !isOutdated
      })
    ) as Data

    return cleaned
  }

  async since() {
    const data = await Browser.storage.local.get(this.sinceKey)

    if (this.sinceKey in data) {
      return data[this.sinceKey] as Since
    }

    return null
  }

  store(data: Data) {
    return Browser.storage.local.set({ [this.storageKey]: data })
  }

  async clear() {
    await Browser.storage.local.remove(this.storageKey)
    await Browser.storage.local.remove(this.sinceKey)
  }

  dispatch(): Promise<Data> {
    return Browser.runtime.sendMessage({ type: this.eventKey })
  }
}

export { BaseListService }
