import EventEmitter from 'events'
import { Agent } from 'https'

import { RateLimiter } from 'limiter'

import { ApiCache } from './cache'

export type TrackResponse<T> = Promise<{
  data: T
  responseHeaders: Headers
  status: number
  statusText: string
  revalidatedData?: TrackResponse<T>
}>

// eslint-disable-next-line @toggl/no-color-literals
type ServiceLevel = 'GREEN' | 'YELLOW' | 'RED' | 'UNKNOWN'

type ApiType = 'accounts' | 'plan' | 'track' | 'reports'

export type OptimizationOptions = {
  /**
   * Enables request deduplication to prevent multiple requests for the same
   * resource to happen in parallel.
   *
   * @default true
   */
  dedupe?: boolean
  /**
   * Enables request caching to prevent refetching data that has been recently fetched
   *
   * @default false
   */
  cache?: boolean
  /**
   * The duration in minutes to how long the data should be cached
   *
   * @default 5
   */
  cacheDurationInMinutes?: number
  /**
   * Enables persistent caching in `IndexDB` in cases where user refreshes the page
   *
   * @default false
   */
  persist?: boolean
  /** Retrieves from cache (if available) while refetching the data from the API */
  staleWhileRevalidate?: boolean
  /** Logs cache actions to the console */
  debug?: boolean
  /** A kill switch to disable all caching */
  disableCaching?: boolean
  /**
   * Will bust the cache if the user rapidly refreshes the page _(safety
   * mechanism if the user is seeing stale data)_
   *
   * By default it will bust the cache **if the user refreshes 3 times within 15
   * seconds** though you can pass a number to change the amount of refreshes
   * that will bust the cache
   *
   * @default false
   */
  bustOnRapidRefresh?: boolean | number
}

type InitOptions = RequestInit & {
  optimizations?: Omit<OptimizationOptions, 'debug' | 'disableCaching'>
}

type NonGetInitOptions = InitOptions & {
  optimizations?: Omit<OptimizationOptions, 'debug' | 'disableCaching'> &
    (
      | {
          /**
           * Labels non-GET requests as cacheable. This is useful for POST
           * requests that fetch data and are free of side-effects.
           */
          cacheable?: boolean
          /** Cannot set `cacheScope` and `cacheable` at the same time */
          cacheScope?: never
        }
      | {
          /** Cannot set `cacheable` and `cacheScope` at the same time */

          cacheable?: never
          /**
           * Will bust the cache for the responses that have the patterns passed
           * to `cacheScope` in their URL
           *
           * By default, it will bust the cache for the exact URL of the same
           * request, as in a `POST/PUT/PATCH/DELETE` request to `/charts` will
           * bust the cached response of the `GET` request of `/charts` if it's
           * in the cache
           *
           * ### Check below for most use-cases for `cacheScope`
           *
           * @example
           *   cacheScope: [] // No URLs will be busted except the exact URL of the request
           *   cacheScope: ['/charts'] // busts all requests that have `/charts` in their URL
           *   cacheScope: [`/charts/${chartId}`] // busts all requests that have `/charts/${chartId}` in their URL
           *   cacheScope: [/\/charts(\?.*)?$/] // busts all requests that end with `/charts` in their URL, with or without query params
           *   cacheScope: [`/charts?organization_id=${organizationId}`] // busts all requests that have `/charts?organization_id=${organizationId}` in their URL
           *   cacheScope: [''] // busts all the cache
           *   cacheScope: ['/charts', '/dashboards', '/reports'] // busts all requests that have `/charts`, `/dashboards` or `/reports` in their URL
           *   cacheScope: [`/${workspaceId}/`] // busts all requests that have `/${workspaceId}/` in their URL
           */
          cacheScope: (string | RegExp)[]
        }
    )
}

// These variables are static because there are several instances of the API client on webapp
// We won't need to use these once permissions are fully available on production
let resolve: (value?: unknown) => void
const promise = new Promise((_resolve) => {
  resolve = _resolve
})
let useWebappPermissions = false

const requestMap = new Map<string, Promise<any>>()

const isClient =
  typeof window !== 'undefined' && typeof localStorage !== 'undefined'

let refreshListenerSet = false

const DEFAULT_OPTIMIZATIONS = {
  dedupe: true,
  cache: false,
  persist: true,
  cacheDurationInMinutes: 5,
  staleWhileRevalidate: false,
  debug: false,
  bustOnRapidRefresh: 2,
} as const

export class Base extends EventEmitter {
  #accountsHost: string
  #agent: Agent | undefined = undefined
  #apiToken: string | undefined
  #blacklist: Set<string> = new Set()
  #headers: Record<string, string>
  #host: string
  #planHost: string
  #reportsHost: string
  #skipPermissionsDependency: boolean
  #teapot = false
  #version: string
  #optimizations: OptimizationOptions

  #rateLimiter = new RateLimiter({
    tokensPerInterval: 10,
    interval: 3000,
  })

  serviceLevel: ServiceLevel = 'UNKNOWN'

  constructor({
    accountsHost = 'https://accounts.toggl.com',
    apiToken,
    headers = {},
    host = 'https://api.track.toggl.com',
    https,
    planHost = 'https://api.plan.toggl.com',
    reportsHost = host,
    skipPermissionsDependency = false,
    version = '',
    optimizations,
  }: Partial<{
    accountsHost: string
    apiToken: string
    headers: Record<string, string>
    host: string
    https: {
      certificateAuthority: string | Buffer | string[] | Buffer[]
    }
    planHost: string
    reportsHost: string
    skipPermissionsDependency: boolean
    version: string
    optimizations: OptimizationOptions
  }> = {}) {
    super()

    this.#accountsHost = accountsHost
    this.#apiToken = apiToken
    this.#headers = headers ?? {}
    this.#host = host
    this.#planHost = planHost
    this.#reportsHost = reportsHost
    this.#skipPermissionsDependency = skipPermissionsDependency
    this.#version = version

    this.#optimizations =
      optimizations ||
      (isClient && localStorage.getItem('api-optimizations')
        ? JSON.parse(localStorage.getItem('api-optimizations') ?? '{}')
        : DEFAULT_OPTIMIZATIONS)

    if (this.#optimizations.cacheDurationInMinutes) {
      ApiCache.setCacheDuration(this.#optimizations.cacheDurationInMinutes)
    }
    if (this.#optimizations.persist) {
      ApiCache.setPersist(this.#optimizations.persist)
    }
    if (isClient && this.#optimizations.debug) {
      ApiCache.setDebug(this.#optimizations.debug)
    }

    if (
      isClient &&
      this.#optimizations.bustOnRapidRefresh &&
      !refreshListenerSet
    ) {
      this.#setupRapidRefreshListener()
    }

    if (https) {
      if (!Agent) {
        throw new Error('Not running in a Node.js environment')
      }

      if (https.certificateAuthority) {
        this.#agent = new Agent({ ca: https.certificateAuthority })
      }
    }
  }

  // @todo: remove this in the cleanup phase.
  throttle = () => this.#throttle()

  delete<T>(
    url: string,
    init?: NonGetInitOptions,
    apiType?: ApiType
  ): TrackResponse<T> {
    this.#invalidateCache(
      url,
      init?.optimizations?.cacheScope,
      init?.optimizations?.persist
    )
    return this.#fetch(
      url,
      {
        ...init,
        method: 'DELETE',
      },
      apiType
    )
  }

  get<T>(url: string, init?: InitOptions, apiType?: ApiType): TrackResponse<T> {
    return this.#fetchWithCachingAndDeduplication(
      url,
      {
        ...init,
        method: 'GET',
      },
      apiType
    )
  }

  head<T>(
    url: string,
    init?: InitOptions,
    apiType?: ApiType
  ): TrackResponse<T> {
    return this.#fetch(
      url,
      {
        ...init,
        method: 'HEAD',
      },
      apiType
    )
  }

  patch<T>(
    url: string,
    init?: NonGetInitOptions,
    apiType?: ApiType
  ): TrackResponse<T> {
    this.#invalidateCache(
      url,
      init?.optimizations?.cacheScope,
      init?.optimizations?.persist
    )
    return this.#fetch(
      url,
      {
        ...init,
        method: 'PATCH',
      },
      apiType
    )
  }

  post<T>(
    url: string,
    init?: NonGetInitOptions,
    apiType?: ApiType
  ): TrackResponse<T> {
    if (!init?.optimizations?.cacheable) {
      this.#invalidateCache(
        url,
        init?.optimizations?.cacheScope,
        init?.optimizations?.persist
      )
      return this.#fetch(
        url,
        {
          ...init,
          method: 'POST',
        },
        apiType
      )
    } else {
      return this.#fetchWithCachingAndDeduplication(
        url,
        {
          ...init,
          method: 'POST',
        },
        apiType
      )
    }
  }

  put<T>(
    url: string,
    init?: NonGetInitOptions,
    apiType?: ApiType
  ): TrackResponse<T> {
    this.#invalidateCache(
      url,
      init?.optimizations?.cacheScope,
      init?.optimizations?.persist
    )
    return this.#fetch(
      url,
      {
        ...init,
        method: 'PUT',
      },
      apiType
    )
  }

  async #throttle(): Promise<void> {
    await this.#rateLimiter.removeTokens(1)
  }

  async #fetch<T>(
    url: string,
    init?: InitOptions,
    apiType?: ApiType
  ): TrackResponse<T> {
    let response

    const headers = new Headers({
      ...this.#headers,
      ...(init?.headers
        ? Array.isArray(init.headers)
          ? Object.fromEntries(init.headers)
          : init.headers
        : {}),
    })

    headers.set(
      'X-User-Agent',
      `@toggl/track-api ${process.env.NEXT_API_VERSION}${
        this.#version ? `; ${this.#version}` : ''
      }`
    )

    if (!headers.has('Content-Type') && !(init?.body instanceof FormData)) {
      headers.set('Content-Type', 'application/json')
    }

    if (this.#apiToken) {
      headers.set(
        'Authorization',
        headers.get('Authorization') ??
          `Basic ${btoa(`${this.#apiToken || ''}:api_token`)}`
      )
    }

    // Plan API's CORS configuration doesnt allow these headers
    if (apiType === 'plan') {
      headers.delete('X-User-Agent')
      headers.delete('App-Version')
      headers.delete('X-Toggl-Client')
      headers.delete('Authorization')
    }

    if (this.#teapot) {
      throw new RequestError("I'm a teapot!", {
        url,
        init,
        response,
      })
    }

    const _apiType: ApiType = url.startsWith('/reports/api/v3/')
      ? 'reports'
      : apiType ?? 'track'

    const baseUrl: Record<ApiType, string> = {
      accounts: this.#accountsHost,
      plan: this.#planHost,
      reports: this.#reportsHost,
      track: this.#host,
    }

    const resource = new URL(url, baseUrl[_apiType])

    if (this.#blacklist.has(resource.pathname)) {
      throw new RequestError('blacklist', {
        url,
        init,
        response,
      })
    }

    let retry

    try {
      const fetchOptions = {
        ...init,
        agent: this.#agent,
        headers,
      }

      this.emit('request', resource, fetchOptions)

      do {
        await this.#throttle()

        response = await fetch(resource.toString(), fetchOptions)

        retry = response.status === 429

        if (retry) {
          this.emit('rateLimited', this.#rateLimiter.tokenBucket.content)

          await this.#rateLimiter.removeTokens(
            this.#rateLimiter.tokenBucket.tokensPerInterval
          )
        }
      } while (retry)

      if (response.status === 401) {
        this.#apiToken = undefined
        this.emit('unauthorized')
        throw new RequestError('unauthorized', {
          url,
          init,
          response,
        })
      }

      if (response.status === 403) {
        this.emit('forbidden')
        throw new RequestError('Forbidden', {
          url,
          init,
          response,
        })
      }

      if (response.status === 410) {
        this.#blacklist.add(resource.pathname)
      }

      if (response.status === 418) {
        this.#teapot = true

        this.emit('teapot')

        throw new RequestError("I'm a teapot!", {
          url,
          init,
          response,
        })
      }

      if (!response.ok) {
        const data = await getErrorResponseData(response)
        throw new RequestError(
          typeof data === 'string' ? data : response.statusText,
          {
            init,
            response,
            url,
            data,
          }
        )
      }

      this.serviceLevel =
        (response.headers.get('X-Service-Level') as ServiceLevel) ?? 'UNKNOWN'

      const responseObj = {
        data: (await getResponseData(response)) as T,
        responseHeaders: response.headers,
        status: response.status,
        statusText: response.statusText,
      }

      this.emit('response', url, responseObj, fetchOptions)

      return responseObj
    } catch (err) {
      let error

      if (err instanceof RequestError) {
        error = err
      } else if (err instanceof Error) {
        error = new RequestError(err.message, {
          init,
          response,
          url,
        })
      } else {
        error = new RequestError('failed', {
          init,
          response,
          url,
        })
      }

      this.listenerCount('error') && this.emit('error', error)
      throw error
    }
  }

  async #fetchWithCachingAndDeduplication<T>(
    url: string,
    init?: InitOptions,
    apiType?: ApiType
  ): TrackResponse<T> {
    const {
      dedupe,
      cache,
      cacheDurationInMinutes,
      staleWhileRevalidate,
      disableCaching,
      persist,
    } = {
      ...DEFAULT_OPTIMIZATIONS,
      ...this.#optimizations,
      ...(init?.optimizations || {}),
    }

    const key = this.#getRequestKey(url, init)
    const cachedResponse =
      !cache || disableCaching ? null : await ApiCache.get(key, persist)

    if (
      cache &&
      !disableCaching &&
      cachedResponse &&
      Date.now() - cachedResponse.timestamp < cacheDurationInMinutes * 60 * 1000
    ) {
      if (staleWhileRevalidate) {
        const revalidatePromise = this.#fetch<T>(url, init, apiType).then(
          (response) => {
            ApiCache.set(
              key,
              { data: response, timestamp: Date.now() },
              persist
            )
            return response
          }
        )

        return Promise.resolve({
          ...cachedResponse.data,
          revalidatedData: revalidatePromise,
        })
      }

      return Promise.resolve(cachedResponse.data)
    }

    if (dedupe && requestMap.has(key)) {
      console.warn(' Duplicate Request', { url, init })
      return requestMap.get(key) as TrackResponse<T>
    }

    const fetchPromise = this.#fetch<T>(url, init, apiType)
    if (dedupe) requestMap.set(key, fetchPromise)

    try {
      const response = await fetchPromise
      if (cache && !disableCaching) {
        ApiCache.set(key, { data: response, timestamp: Date.now() }, persist)
      }
      return response
    } finally {
      requestMap.delete(key)
    }
  }

  #invalidateCache(
    url: string,
    cacheScope?: (string | RegExp)[],
    inPersistentCache?: boolean
  ) {
    ApiCache.removeByUrl(url, inPersistentCache)
    if (cacheScope) {
      this.invalidateCacheByPatterns(cacheScope, inPersistentCache)
    }
  }

  invalidateCacheByPatterns(
    cacheScope: (string | RegExp)[],
    inPersistentCache?: boolean
  ) {
    cacheScope.forEach((scope) =>
      ApiCache.removeByPattern(scope, inPersistentCache)
    )
  }

  #getRequestKey(url: string, init?: InitOptions): string {
    return JSON.stringify({ url, init: { ...init, optimizations: undefined } })
  }

  setOptimizations(optimizations: OptimizationOptions) {
    this.#optimizations = {
      ...DEFAULT_OPTIMIZATIONS,
      ...optimizations,
    }

    if (this.#optimizations.cacheDurationInMinutes) {
      ApiCache.setCacheDuration(this.#optimizations.cacheDurationInMinutes)
    }

    if (this.#optimizations.persist) {
      ApiCache.setPersist(this.#optimizations.persist)
    }

    if (isClient && this.#optimizations.debug) {
      ApiCache.setDebug(this.#optimizations.debug)
    }

    if (isClient) {
      localStorage.setItem(
        'api-optimizations',
        JSON.stringify(this.#optimizations)
      )
    }
  }

  bustCache() {
    ApiCache.bust()
  }

  wipeCache() {
    if (isClient) {
      localStorage.removeItem('api-optimizations')
    }
    ApiCache.wipe()
  }

  #setupRapidRefreshListener() {
    if (typeof window === 'undefined') return

    refreshListenerSet = true

    const RAPID_REFRESH_WINDOW = 30 * 1000 // 30 seconds
    const REFRESH_TRACKING_KEY = 'api-refresh-tracking'
    const RAPID_REFRESH_THRESHOLD =
      typeof this.#optimizations.bustOnRapidRefresh === 'number'
        ? this.#optimizations.bustOnRapidRefresh
        : 3

    window.addEventListener('beforeunload', () => {
      const data = sessionStorage.getItem(REFRESH_TRACKING_KEY)
      const refreshData = data
        ? JSON.parse(data)
        : { refreshCount: 0, lastRefreshTime: 0 }
      const now = Date.now()

      if (now - refreshData.lastRefreshTime > RAPID_REFRESH_WINDOW) {
        refreshData.refreshCount = 1
      } else {
        refreshData.refreshCount += 1
      }

      refreshData.lastRefreshTime = now
      sessionStorage.setItem(REFRESH_TRACKING_KEY, JSON.stringify(refreshData))

      if (refreshData.refreshCount >= RAPID_REFRESH_THRESHOLD) {
        this.bustCache()
        refreshData.refreshCount = 0
        sessionStorage.setItem(
          REFRESH_TRACKING_KEY,
          JSON.stringify(refreshData)
        )
      }
    })
  }

  /** Current API token. */
  apiToken() {
    return this.#apiToken
  }

  /** Update the current API token. */
  setApiToken(token?: string) {
    this.#apiToken = token
  }

  // Setter for permissions, we won't need this once permissions are fully available on production
  setPermissions(value: boolean) {
    useWebappPermissions = value
    resolve()
  }

  // Getter for permissions, we won't need this once permissions are fully available on production
  async getPermissions() {
    if (!this.#skipPermissionsDependency) {
      await promise
    }
    return useWebappPermissions ? 'true' : 'false'
  }

  /**
   * Creates a new instance, with identical properties. Useful if you want to
   * reuse client settings.
   */
  newAPI() {
    const next = new Base()
    next.#apiToken = this.#apiToken
    // TODO: Should be a different instance.
    next.#agent = this.#agent
    // TODO: Should be a different instance.
    next.#headers = this.#headers
    next.#host = this.#host
    next.#reportsHost = this.#reportsHost
    next.#version = this.#version
    next.#teapot = this.#teapot
    next.#rateLimiter = new RateLimiter({
      tokensPerInterval: this.#rateLimiter.tokenBucket.tokensPerInterval,
      interval: this.#rateLimiter.tokenBucket.interval,
    })
    return next
  }
}

export class RequestError extends Error {
  name = 'RequestError'

  url: string
  init: InitOptions | void
  response: Response | void
  data: unknown

  constructor(
    message: string,
    options: {
      url: string
      init?: InitOptions
      response?: Response
      data?: unknown
    }
  ) {
    super(message)
    this.url = options.url
    this.init = options.init
    this.response = options.response
    this.data = options.data
  }

  /**
   * Fingerprint for grouping. See
   * https://docs.sentry.io/product/sentry-basics/grouping-and-fingerprints/
   */
  get fingerprint() {
    return [
      this.init?.method,
      getUrlFingerprint(this.url),
      this.response?.status,
    ]
  }
}

/*
 * The role of this method is to return the response data
 */
const getResponseData = (response: Response): Promise<unknown> => {
  const contentType = response.headers.get('content-type') ?? ''

  if (
    /application\/json/.test(contentType) &&
    (response.status === 204 || response.headers.get('content-length') === '0')
  ) {
    return Promise.resolve(null)
  }

  if (/application\/json/.test(contentType)) {
    return response.json()
  }

  if (!contentType || /^text\/(plain|html)|charset=utf-8$/.test(contentType)) {
    return response.text()
  }

  return response.arrayBuffer()
}

/*
 * The role of this method is to return the error response
 * Due to api inconsistencies we need to parse the data to ensure
 * on response type
 * @param {Response} response
 * @return {string} the error message
 */
const getErrorResponseData = async (response: Response): Promise<string> => {
  const contentType = response.headers.get('content-type') || ''

  if (
    /application\/json/.test(contentType) &&
    response.headers.get('content-length') === '0'
  ) {
    return Promise.resolve(
      'There was an error. If this error persists, please contact support.'
    )
  }

  let responseMessage

  if (/application\/json/.test(contentType)) {
    const responseText = await response.text()
    try {
      responseMessage = JSON.parse(responseText)
    } catch (error) {
      return responseText
    }
  } else {
    responseMessage = await response.text()
  }

  return responseMessage
}

export const getUrlFingerprint = (url: string): string =>
  url
    .split('/')
    .map((part) => part.replace(/^\d+$/, ':id'))
    .join('/')
    .replace(/\?.*$/, '')
