  client: Endpoints['client'] = {
    archive: (client, optimizations) =>
      this.post(
        `/api/v9/workspaces/${client.wid}/clients/${client.id}/archive`,
        { optimizations }
      ),
    create: (client, optimizations) =>
      this.getPermissions().then((permissions) =>
        this.post(`/api/v9/workspaces/${client.wid}/clients`, {
          body: JSON.stringify(client),
          ...useCustomHeaders([() => withPermissions(permissions)]),
          optimizations,
        })
      ),
    data: (workspaceId, clientIds) =>
      this.post(`/api/v9/workspaces/${workspaceId}/clients/data`, {
        body: JSON.stringify({ client_ids: clientIds }),
      }),
    delete: (client, optimizations) =>
      this.delete(`/api/v9/workspaces/${client.wid}/clients/${client.id}`, {
        optimizations,
      }),
    fetch: (workspaceId, filter, options, optimizations) => {
      const status =
        filter?.active === 'both'
          ? 'both'
          : filter?.active === true
          ? 'active'
          : filter?.active === false
          ? 'archived'
          : 'active'

      const searchParams: {
        name?: string
        status: string
        page?: string
        per_page?: string
      } = {
        status,
      }

      if (filter?.name) {
        searchParams.name = filter.name
      }

      if (options) {
        searchParams.page = options.page?.toString()
        searchParams.per_page = options.per_page?.toString()
      }

      const params = new URLSearchParams(searchParams)

      return this.getPermissions().then((permissions) => {
        return this.get(
          `/api/v9/workspaces/${workspaceId}/clients?${params.toString()}`,
          {
            ...useCustomHeaders([() => withPermissions(permissions)]),
            optimizations,
          }
        )
      })
    },
    restore: (client, projects, optimizations) =>
      this.getPermissions().then((permissions) =>
        this.post(
          `/api/v9/workspaces/${client.wid}/clients/${client.id}/restore`,
          {
            body: JSON.stringify(
              projects === 'all' ? { restore_all_projects: true } : { projects }
            ),
            ...useCustomHeaders([() => withPermissions(permissions)]),
            optimizations,
          }
        )
      ),
    update: (client, optimizations) =>
      this.getPermissions().then((permissions) =>
        this.put(`/api/v9/workspaces/${client.wid}/clients/${client.id}`, {
          body: JSON.stringify(client),
          ...useCustomHeaders([() => withPermissions(permissions)]),
          optimizations,
        })
      ),
  }

