import { API } from '@toggl/track-api'

const apiClient = new API({
  host: process.env.API_URL,
  version: `@toggl/track-extension ${process.env.VERSION}`,
  headers: {
    'X-Toggl-Client': 'track-extension',
  },
  skipPermissionsDependency: true,
  accountsHost: process.env.TOGGL_ACCOUNTS_API,
})

export default apiClient
