import apiClient from '@src/background/_internal/_api'
import { with403Handler } from '@src/shared/utils/with403Handler'

import { type Project, type Since } from '@toggl/track-types'

import { BaseListService } from '../lib/BaseListService'
import { useBrowserStorageLocal } from '../lib/use-browser-storage'

class ListService<K extends string> extends BaseListService<
  Record<string, Project>,
  K
> {
  invoke = with403Handler(
    async (since: Since, args?: { returnApiData?: boolean }) => {
      const data = await apiClient.me.projects.list(since).then(({ data }) => {
        return Object.fromEntries(
          data.map((item) => [item.id, item])
        ) as unknown as Record<string, Project>
      })

      // Update the internal since key
      await this.arrange(since, data)

      return args?.returnApiData ? data : this.retrieve()
    }
  )
}

const ProjectsListService = new ListService('FETCH_PROJECTS_V4')

type StorageData = Awaited<ReturnType<typeof ProjectsListService['invoke']>>

const useProjectsListStorage = (initial: StorageData | null = null) => {
  return useBrowserStorageLocal<StorageData>(
    ProjectsListService.storageKey,
    initial
  )
}

export { ProjectsListService, useProjectsListStorage }
