import apiClient from '@src/background/_internal/_api'
import { with403Handler } from '@src/shared/utils/with403Handler'

import { type CurrentUser } from '@toggl/track-types'

import { BaseService } from '../lib/BaseService'
import { useBrowserStorageLocal } from '../lib/use-browser-storage'

class Service<K extends string> extends BaseService<CurrentUser, K> {
  invoke = with403Handler(
    async (opts: { store?: boolean } = { store: false }) => {
      const { data } = await apiClient.me.get()

      // Insert into storage
      if (opts.store) {
        await this.store(data)
      }

      return data
    }
  )

  // This could be an atomic service as well, but it's way too simple.
  // Maybe can be moved in the future.
  public readonly logoutKey = 'USER_LOGOUT_E' as const

  async logout() {
    await apiClient.accounts.sessions.logout()
    await this.clear()
  }
}

const UsersService = new Service('FETCH_USER_V4')

type StorageData = Awaited<ReturnType<typeof UsersService['invoke']>>

const useUserStorage = (initial: StorageData | null = null) => {
  return useBrowserStorageLocal<StorageData>(UsersService.storageKey, initial)
}

export { UsersService, useUserStorage }
