import apiClient from '@src/background/_internal/_api'
import { with403Handler } from '@src/shared/utils/with403Handler'

import { type Since, type Workspace } from '@toggl/track-types'

import { BaseListService } from '../lib/BaseListService'
import { useBrowserStorageLocal } from '../lib/use-browser-storage'

class ListService<K extends string> extends BaseListService<
  Record<string, Workspace>,
  K
> {
  invoke = with403Handler(async (since: Since) => {
    const data = await apiClient.me.workspaces.list(since).then(({ data }) => {
      return Object.fromEntries(
        data.map((item) => [item.id, item])
      ) as unknown as Record<string, Workspace>
    })

    // Update the internal since key
    await this.arrange(since, data)

    return this.retrieve()
  })
}

const WorkspacesListService = new ListService('FETCH_WORKSPACES_V4')

type StorageData = Awaited<ReturnType<typeof WorkspacesListService['invoke']>>

const useWorkspacesListStorage = (initial: StorageData | null = null) => {
  return useBrowserStorageLocal<StorageData>(
    WorkspacesListService.storageKey,
    initial
  )
}

// Util to use a single workspace.
const useWorkspace = (id?: Workspace['id'] | null) => {
  const [list] = useWorkspacesListStorage()

  if (id && list && id in list) {
    return list[id]
  }

  return null
}

export { WorkspacesListService, useWorkspacesListStorage, useWorkspace }
