export const parametrize = (params: Record<string, any>): string => {
  const filteredParams = { ...params }
  for (const param in params) {
    if (
      filteredParams[param] === undefined ||
      filteredParams[param] === null ||
      filteredParams[param] === ''
    ) {
      delete filteredParams[param]
    }
  }
  const paramsString = new URLSearchParams(filteredParams).toString()
  return paramsString ? `?${paramsString}` : ''
}

/**
 * @example
 *   useCustomHeaders([withDummyHeader, withAnotherDummyHeader])
 *   will return
 *   {
 *   headers: {
 *   'dummy-header': 'dummy-value',
 *   'another-dummy-header': 'another-d
 *   }
 *   }
 *
 * @param {Function[]} headersArray - Array of functions that return an object
 *   with the headers to be added
 * @returns {Object} Object with the headers to be added to the request
 */
export const useCustomHeaders = (
  headersArray: [() => { [key: string]: string }]
) => {
  return {
    headers: {
      ...headersArray.reduce((acc, header) => {
        return { ...acc, ...header() }
      }, {}),
    },
  }
}

/**
 * @example
 *   withPermissions('true')
 *   will return
 *   {
 *   'x-toggl-permissions': 'true'
 *   }
 *
 * @param {string} usePermissions - String that represents the value of the
 *   x-toggl-permissions header, please note that the parameter won't be needed
 *   once permissions are fully available on production
 * @returns {Object} Object with the x-toggl-permissions header to be added to the request
 */
export const withPermissions = (
  usePermissions: 'false' | 'true'
): { 'x-toggl-permissions'?: 'true' } => {
  return usePermissions === 'true'
    ? { 'x-toggl-permissions': usePermissions }
    : {}
}
