import isEqual from 'lodash.isequal'
import * as React from 'react'
import Browser from 'webextension-polyfill'

type Area = 'local' | 'sync'

export const storage = {
  get: async (key: string, defaultValue: any, area: Area) => {
    const keyObj = defaultValue === undefined ? key : { [key]: defaultValue }
    const items = await Browser.storage[area].get(keyObj)
    return items[key]
  },
  set: (key: string, value: any, area: Area) => {
    return Browser.storage[area].set({ [key]: value })
  },
}

export function useBrowserStorage<T>(
  key: string,
  initial: T | null,
  area: Area,
  equalityFn: (oldValue: T | null, newValue: T | null) => boolean = isEqual
) {
  const [initialValue] = React.useState(initial)
  const [storageArea] = React.useState(area)
  const [state, setState] = React.useState<T | null>(initialValue)
  const [isPersistent, setIsPersistent] = React.useState(true)
  const [error, setError] = React.useState('')
  const [isResolved, setIsResolved] = React.useState(false)

  React.useEffect(() => {
    storage
      .get(key, initialValue, storageArea)
      .then((res) => {
        setState(res as T)
        setIsPersistent(true)
        setError('')
      })
      .catch((error) => {
        setIsPersistent(false)
        setError(error)
      })
      .finally(() => {
        setIsResolved(true)
      })
  }, [key, initialValue, storageArea])

  const updateValue = React.useCallback(
    (newValue: (T | null) | ((state: T | null) => T | null)) => {
      const toStore =
        typeof newValue === 'function'
          ? (newValue as (state: T | null) => T)(state)
          : newValue

      storage
        .set(key, toStore, storageArea)
        .then(() => {
          setIsPersistent(true)
          setError('')
        })
        .catch((error) => {
          setState(toStore)
          setIsPersistent(false)
          setError(error)
        })
    },
    [storageArea, key, state]
  )

  React.useEffect(() => {
    const onChange = (changes) => {
      if (key in changes) {
        const change = changes[key]
        const isValueStored = 'newValue' in change

        /**
         * This will save your life by preventing unnecessary re-renders. For
         * every change in storage, notice that by default is the isEqual from lodash.
         */
        if (equalityFn(change?.oldValue, change?.newValue)) {
          return
        }

        if (isValueStored) {
          setState(change.newValue)
        } else {
          setState(initialValue)
        }

        setIsPersistent(isValueStored)
        setError('')
      }
    }

    Browser.storage[area].onChanged.addListener(onChange)

    return () => {
      Browser.storage[area].onChanged.removeListener(onChange)
    }
  }, [key, storageArea, initialValue, equalityFn, area])

  return React.useMemo(() => {
    return [
      state,
      updateValue,
      { isPersistent, error, isCacheLoading: !isResolved },
    ] as const
  }, [state, updateValue, isPersistent, error, isResolved])
}

export function useBrowserStorageLocal<T>(
  key: string,
  initial: T | null,
  equalityFn: (oldValue: T | null, newValue: T | null) => boolean = isEqual
) {
  return useBrowserStorage<T>(key, initial, 'local', equalityFn)
}

export function useBrowserStorageSync<T>(
  key: string,
  initial: T | null,
  equalityFn: (oldValue: T | null, newValue: T | null) => boolean = isEqual
) {
  return useBrowserStorage<T>(key, initial, 'sync', equalityFn)
}
