import { UsersService } from '@src/v4/services/UsersService'
import Browser from 'webextension-polyfill'

import { RequestError } from '@toggl/track-api'

// eslint-disable-next-line @typescript-eslint/no-explicit-any
export function with403Handler<T>(fn: (...args: any[]) => Promise<T>) {
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  return async function callWith403(this: any, ...args: any[]): Promise<T> {
    try {
      return await fn.apply(this, args)
    } catch (error) {
      if (
        error instanceof RequestError &&
        (error.response?.status === 403 || error.response?.status === 401)
      ) {
        await Browser.storage.local.clear()
        await UsersService.store(null) // Trigger the listeners.
      }

      // Then, rethrow the error to block the execution.
      throw error
    }
  }
}
