import * as Sentry from '@sentry/browser'

const DB_NAME = 'apiCache'
const DB_VERSION = 1
const STORE_NAME = 'responses'

const CACHE_CLEANUP_INTERVAL = 30 * 60 * 1000 // 30 minute
let CACHE_DURATION = 5 * 60 * 1000 // 5 minutes

const cacheMemoryStorage = new Map<string, { data: any; timestamp: number }>()
let PERSIST = false

let DEBUG = false

export const setPersist = (persist: boolean) => {
  PERSIST = persist
}

const setCacheDuration = (durationInMinutes: number) => {
  CACHE_DURATION = durationInMinutes * 60 * 1000
}

const setDebug = (debug: boolean) => {
  DEBUG = debug
}

const debugLogger = (message: string, data?: any) => {
  console.log(
    `%c [API-CACHE] ${message}`,
    'font-weight: bold; font-size: 1rem; color: #FDE7DF; background: #412A4C; padding: 2px 4px; border-radius: 4px;'
  )
}

const openDB = () => {
  return new Promise<IDBDatabase>((resolve, reject) => {
    const request = indexedDB.open(DB_NAME, DB_VERSION)

    request.onupgradeneeded = (event) => {
      const db = (event.target as IDBOpenDBRequest).result
      if (!db.objectStoreNames.contains(STORE_NAME)) {
        db.createObjectStore(STORE_NAME, { keyPath: 'key' })
      }
    }

    request.onsuccess = (event) => {
      resolve((event.target as IDBOpenDBRequest).result)
    }

    request.onerror = (event) => {
      reject((event.target as IDBOpenDBRequest).error)
    }
  })
}

const getTransaction = async (mode: IDBTransactionMode) => {
  const db = await openDB()
  return db.transaction(STORE_NAME, mode).objectStore(STORE_NAME)
}

const serializeHeaders = (headers: Headers): Record<string, string> => {
  const serialized: Record<string, string> = {}
  headers.forEach((value, key) => {
    serialized[key] = value
  })
  return serialized
}

const deserializeHeaders = (headersObj: Record<string, string>) => {
  const headers = new Headers()
  for (const key in headersObj) {
    headers.append(key, headersObj[key])
  }
  return headers
}

const setCache = async (
  key: string,
  response: any,
  persist: boolean = PERSIST
) => {
  if (persist) {
    const store = await getTransaction('readwrite')
    const { data, responseHeaders } = response
    store.put({
      key,
      data: {
        ...data,
        responseHeaders: responseHeaders
          ? serializeHeaders(responseHeaders)
          : {},
      },
      timestamp: Date.now(),
    })
  } else {
    const { data, responseHeaders } = response
    cacheMemoryStorage.set(key, {
      data: {
        ...data,
        responseHeaders: responseHeaders
          ? serializeHeaders(responseHeaders)
          : {},
      },
      timestamp: Date.now(),
    })
  }

  if (DEBUG) {
    debugLogger(`Cache entry for ${JSON.parse(key)?.url || key} stored `)
  }
}

const getCache = async (key: string, persist: boolean = PERSIST) => {
  if (persist) {
    const store = await getTransaction('readonly')
    return new Promise<any>((resolve, reject) => {
      const request = store.get(key)
      request.onsuccess = (event) => {
        const result = (event.target as IDBRequest).result
        if (result) {
          resolve({
            ...result,
            data: {
              ...result.data,
              responseHeaders: deserializeHeaders(result.data.responseHeaders),
            },
          })

          if (DEBUG) {
            debugLogger(`Cache hit for ${JSON.parse(key)?.url || key} `)
          }
        } else {
          resolve(null)

          if (DEBUG) {
            debugLogger(`Cache miss for ${JSON.parse(key)?.url || key} `)
          }
        }
      }
      request.onerror = (event) => {
        reject((event.target as IDBRequest).error)
      }
    })
  } else {
    const cacheEntry = cacheMemoryStorage.get(key)
    if (cacheEntry) {
      if (DEBUG) {
        debugLogger(`Cache hit for ${JSON.parse(key)?.url || key} `)
      }
      return {
        ...cacheEntry,
        data: {
          ...cacheEntry.data,
          responseHeaders: deserializeHeaders(cacheEntry.data.responseHeaders),
        },
      }
    } else {
      if (DEBUG) {
        debugLogger(`Cache miss for ${JSON.parse(key)?.url || key} `)
      }
    }

    return null
  }
}

const removeCache = async (key: string) => {
  const store = await getTransaction('readwrite')
  store.delete(key)

  if (DEBUG) {
    debugLogger(`Cache entry for ${JSON.parse(key)?.url || key} removed `)
  }
}

const bustAllCache = async () => {
  const store = await getTransaction('readwrite')
  store.clear()

  if (DEBUG) {
    debugLogger('All cache entries removed ')
  }
}

const removeExpiredCache = async () => {
  const store = await getTransaction('readwrite')
  const request = store.openCursor()
  const now = Date.now()

  request.onsuccess = (event) => {
    const cursor = (event.target as IDBRequest<IDBCursorWithValue>).result
    if (cursor) {
      const { timestamp } = cursor.value
      if (now - timestamp > CACHE_DURATION) {
        store.delete(cursor.key)

        if (DEBUG) {
          debugLogger(`Expired cache entry ${cursor.key} removed `)
        }
      }
      cursor.continue()
    }
  }

  request.onerror = (event) => {
    if (DEBUG) {
      console.error(
        'Failed to clean up expired cache:',
        (event.target as IDBRequest).error
      )

      Sentry.captureException(
        new Error(
          'Failed to clean up expired cache: ' +
            (event.target as IDBRequest).error
        )
      )
    }
  }
}

const removeCacheEntriesByURL = async (
  url: string,
  inPersistentCache: boolean = PERSIST
) => {
  if (inPersistentCache) {
    const store = await getTransaction('readwrite')

    const request = store.openCursor()

    request.onsuccess = (event) => {
      const cursor = (event.target as IDBRequest<IDBCursorWithValue>).result

      if (cursor) {
        const { url: cachedUrl } = JSON.parse(String(cursor.key))

        const sanitizedCachedUrl = cachedUrl.split('?')[0]

        if (sanitizedCachedUrl === url || cachedUrl === url) {
          cursor.delete()

          if (DEBUG) debugLogger(`Cache entry for ${cachedUrl} removed `)
        }

        cursor.continue()
      }
    }

    request.onerror = (event) => {
      if (DEBUG) {
        console.error(
          'Failed to delete cache entry:',
          (event.target as IDBRequest).error
        )
      }
    }
  } else {
    cacheMemoryStorage.forEach((_, key) => {
      const { url: cachedUrl } = JSON.parse(key)

      const sanitizedCachedUrl = cachedUrl.split('?')[0]

      if (sanitizedCachedUrl === url) {
        cacheMemoryStorage.delete(key)

        if (DEBUG) debugLogger(`Cache entry for ${cachedUrl} removed `)
      }
    })
  }
}

const removeCacheEntriesByPattern = async (
  pattern: string | RegExp,
  persist: boolean = PERSIST
) => {
  if (persist) {
    const store = await getTransaction('readwrite')

    const request = store.openCursor()

    request.onsuccess = (event) => {
      const cursor = (event.target as IDBRequest<IDBCursorWithValue>).result

      if (cursor) {
        const { url: cachedUrl } = JSON.parse(String(cursor.key))

        if (
          (pattern instanceof RegExp && pattern.test(cachedUrl)) ||
          cachedUrl.includes(pattern)
        ) {
          cursor.delete()

          if (DEBUG) debugLogger(`Cache entry for ${cachedUrl} removed `)
        }

        cursor.continue()
      }
    }

    request.onerror = (event) => {
      if (DEBUG) {
        console.error(
          'Failed to delete cache entry:',
          (event.target as IDBRequest).error
        )
      }
    }
  } else {
    cacheMemoryStorage.forEach((_, key) => {
      const { url: cachedUrl } = JSON.parse(key)

      if (
        (pattern instanceof RegExp && pattern.test(cachedUrl)) ||
        cachedUrl.includes(pattern)
      ) {
        cacheMemoryStorage.delete(key)

        if (DEBUG) debugLogger(`Cache entry for ${cachedUrl} removed `)
      }
    })
  }
}

let cacheCleanupInterval: NodeJS.Timeout

const setupCacheCleanupInterval = () => {
  cacheCleanupInterval = setInterval(() => {
    removeExpiredCache().catch((error) => {
      if (DEBUG) {
        console.error('Failed to clean up expired cache:', error)
      }
    })
  }, CACHE_CLEANUP_INTERVAL)
}

const wipeCache = () => {
  bustAllCache()
  clearInterval(cacheCleanupInterval)

  if (DEBUG) {
    debugLogger('Cache wiped ')
  }
}

export const ApiCache = {
  set: setCache,
  get: getCache,
  remove: removeCache,
  removeByUrl: removeCacheEntriesByURL,
  removeByPattern: removeCacheEntriesByPattern,
  /** Removes all cache content while still keeping it active */
  bust: bustAllCache,
  /** Removes all cache content while removing all cache listeners */
  wipe: wipeCache,
  setCacheDuration,
  setPersist,
  setDebug,
}

if (typeof window === 'undefined' || typeof indexedDB === 'undefined') {
  // If not running on client side, replace all methods with empty functions
  for (const method in ApiCache) {
    if (method === 'get') {
      // Return null instead of undefined for 'get', to match the cache miss result
      ApiCache.get = () => new Promise((resolve) => resolve(null))
    } else {
      ApiCache[method as keyof typeof ApiCache] = async () => {}
    }
  }
}

setupCacheCleanupInterval()
