import apiClient from '@src/background/_internal/_api'
import { with403Handler } from '@src/shared/utils/with403Handler'
import isEqual from 'lodash.isequal'

import { type TimeEntryV9 } from '@toggl/track-types'

import { BaseService } from '../lib/BaseService'
import { useBrowserStorageLocal } from '../lib/use-browser-storage'

class CurrentService<K extends string> extends BaseService<TimeEntryV9, K> {
  invoke = with403Handler(
    async (opts: { store?: boolean } = { store: false }) => {
      const { data } = await apiClient.me.timeEntries.current()

      // Insert into storage
      if (opts.store) {
        await this.store(data)
      }

      return data
    }
  )

  fromApi(data: TimeEntryV9 | null) {
    return data
      ? Object.assign(
          {},
          {
            // at: data.at,
            billable: data.billable,
            description: data.description,
            duration: data.duration,
            duronly: data.duronly,
            id: data.id,
            project_id: data.project_id,
            server_deleted_at: data.server_deleted_at,
            shared_with: data.shared_with,
            start: new Date(data.start).toISOString(),
            stop: data.stop,
            tag_ids: data.tag_ids ?? [],
            tags: data.tags,
            task_id: data.task_id,
            user_id: data.user_id,
            workspace_id: data.workspace_id,
          }
        )
      : null
  }
}

const CurrentTEService = new CurrentService('FETCH_CURRENT_TE_V4')

type StorageData = Awaited<ReturnType<typeof CurrentTEService['invoke']>>

const useCurrentTimeEntryStorage = (initial: StorageData | null = null) => {
  return useBrowserStorageLocal<StorageData>(
    CurrentTEService.storageKey,
    initial,
    (oldValue, newValue) => {
      return isEqual(
        CurrentTEService.fromApi(oldValue),
        CurrentTEService.fromApi(newValue)
      )
    }
  )
}

export { CurrentTEService, useCurrentTimeEntryStorage }
